.todoCard {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 12px;

  padding: 16px;
  width: 60%;

  border-radius: 16px;
  border: 1px solid rgb(200, 200, 200);
}

.text {
  word-break: break-word;
}

.date {
  display: flex;
  align-items: center;
  gap: 8px;
}

.dateDescr {
  font-size: 16px;
}
