.section {
  padding: 48px 0;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 1280px;
}

.todosTitle {
  margin-bottom: 20px;

  font-size: 30px;
  line-height: calc(38 / 30);
  text-align: center;
}

.todoList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;

  margin: 0 auto;
}

.notification {
  text-align: center;
}
